  font-family: "Raleway", sans-serif;
}
.Projects {
  position: absolute;
  left: 350px;
  padding-top: 200px;
  top: 1600px;
}
.Projects h1 {
  font-size: 50px;
  color: #41443f;
  font-family: "Oswald", sans-serif;
  position: absolute;
}
.Skills h1 {
  font-size: 50px;
  color: #41443f;
  font-family: "Oswald", sans-serif;
}
.Skills h3 {
  font-family: "Oswald", sans-serif;
  font-weight: 100;
  font-size: larger;
  color: #737a6f;
}

.Skills {
  position: absolute;
  left: 350px;
  padding-top: 200px;
  top: 2400px;
  text-align: left;
  height: 600px;
}
.About h1 {
  font-family: "Oswald", sans-serif;
  font-size: 75px;
  display: inline;
  padding: 0;
}
.About h3 {
  font-family: "Oswald", sans-serif;
  font-size: 18px;
  font-weight: 100;
  color: #747a70;
  padding: 0;
  margin: 0;
  text-align: left;
  display: inline;
}
.Email {
  font-family: "Oswald", sans-serif;
  font-size: 18px;
  font-weight: 100;
  color: #469931;
  display: inline;
  padding: 0;
  margin: 0;
  text-align: left;
}
.About h5 {
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  color: #747a70;
  display: block;
  width: 900px;
  margin-top: 40px;
  text-align: left;
  margin-bottom: 60px;
}
.last {
  color: #469931;
}
.Icondiv {
  display: inline;
  background-color: #4c5049;
  color: white;
  padding-top: 22px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 12px;
  margin-right: 40px;
  border-radius: 50%;
}
.Icondiv a {
    color: white;
}
.Icondiv a:visited {
    color: white;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto;
  padding: 3px;
  align-items: center;
  width: 100%;
  padding-top: 200px;
}
.grid-item {
  padding: 20px;
  font-size: 30px;
  text-align: center;
}
.grid-item img {
  width: 200px;
  border-radius: 7px;
}

.skillz {
  display: inline;
  padding-left: 10px;
  width: 20px;
  color: #747a70;
}

.mainSkill {
  padding-top: 20px;
  padding-bottom: 20px;
}

.Skills ul li {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  color: #747a70;
}

.Skills ul li h5 {
  margin: 0;
  padding: 0;
}

