.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


.flex-container {
  display: flex;
}



.flex-child:first-child {
  background-color: #469931;
  width: 300px;
  height: 100%;
  position: fixed;
  
  
} 
.flex-child.magenta {
  width: 100%;
  height: 2000px;

}
.Burger {
  display: none;
}
.flex-child.green img {
  width: 150px;
  border-radius: 60%;
  position: fixed;
  left: 60px;
  margin-top: 150px;
  border: 10px solid rgba(190, 190, 190, .3);



}
.linkdiv {
  position: fixed;
  bottom: 210px;
  display: block;
}

.links {
  width: 100%;
  position: relative;
  float: left;
  font-size: larger;
  margin-top: 15px;
  color: #7bc569;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  right: 15px;
  
  

}



.links:active {
  color: white;
  text-decoration: none;
}


hr {
  margin-top: 800px;
  height: 0.1px;
  width: 100%;
  background-color: rgb(202, 199, 199);
  border: 0;
  
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 768px) {
  .flex-child.green {
    width: 100%;
    height: 60px;
    position: fixed;
  }
  .flex-child.green img {
    width: 30px;
    border-radius: 60%;
    position: fixed;
    left: 10px;
    margin-top: 9px;
    border: 5px solid rgba(190, 190, 190, .3);
  }
  .linkdiv {
    position: fixed;
    top: 60px;
    right: 0px;
    width: 130px;
    background-color:  #469931;
    height: 175px;
    display: none;
  }

  .flex-child.magenta {
    height: 2000px;
    width: 0px; 
  
  }

  .Burger {
    display: block;
    float: right;
    padding: 10px;
    color: rgb(159, 218, 114);
  }

  hr {
    margin-top: 800px;
    height: 0.1px;
    background-color: rgb(202, 199, 199);
    border: 0;
    
  }
  .flex-container {
    display: grid;
   

  }
  .links {
    width: 100%;
    margin-left: 20px;
    position: relative;
    float: left;
    font-size: larger;
    margin-top: 15px;
    color: #7bc569;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    right: 15px;
    
    
  
  }




}

